<script>
    import {ImageModel} from '../models/images'

    export default {
        name: "ImageTile",
        data: () => ({
            modalActive: false,
            colors: [
                "bg-red-800",
                "bg-gray-800",
                "bg-indigo-800",
                "bg-pink-800",
                "bg-teal-800",
                "bg-green-800",
                "bg-blue-800",
                "bg-orange-800",
                "bg-yellow-800",
                "bg-purple-800",
            ],
            prevent: true
        }),
        props: {
            imageData: {
                required: true,
                type: ImageModel
            },
            short: Boolean,
            long: Boolean,
            mobile: Boolean,
            square: Boolean,
            normal: {
                type: Boolean,
                default: true
            }
        },
        methods: {
            background() {
                return this.imageData
            },
            toggleModal() {
                this.modalActive = !this.modalActive
            },
            randomColor() {
                //return 'bg-red-500'
                return this.colors[Math.floor(Math.random() * this.colors.length)]
            },
            lazyload() {

                let b = this.$el.getBoundingClientRect()

                if (b.top + this.$el.clientHeight >= 0 && b.bottom - this.$el.clientHeight <= window.innerHeight) {
                    this.prevent = ''
                    window.removeEventListener('scroll', this.lazyload)

                }
            }
        },
        computed: {
            backgroundImage: () => this.normal
        },
        mounted() {
            this.lazyload()
            window.addEventListener('scroll', this.lazyload)
        }
    }
</script>


