export class ImageModel {
    constructor(id, url, author, normal=false, short=false, long=false) {
        this.id = id
        this.url = url
        this.author = author
        this.short=short
        this.long = long
        this.normal = !(short || long) && normal
    }

    setShort() {
        this.short = true
        this.long = this.normal = false
    }
    setLong() {
        this.long = true
        this.short = this.normal = false
    }
    setNormal() {
        this.normal = true
        this.long = this.short = false
    }



}

