        <div style="max-width:82rem" class="mx-auto flex my-8 md:px-2 ">

            <div class="flex flex-col w-full md:w-1/2 lg:w-1/3">
                <ImageTile class="tile hidden md:block" v-for="(item, index) in row1.images" :key="index"
                           :image-data="item"
                           :short="item.short" :normal="item.normal" :long="item.long"></ImageTile>
                <ImageTile class="tile md:hidden" v-for="(item, index) in row1.images" :key="index" :image-data="item"
                           mobile
                ></ImageTile>
