<script>
    import {ImageModel} from "../models/images";
    import {RowModel} from "../models/row";
    import ImageTile from "../components/ImageTile";

    export default {
        name: 'Home',
        data: () => ({
            visible: false,
            images: [
                new ImageModel(1, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(2, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(3, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(4, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(5, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(6, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(7, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(8, 'https://source.unsplash.com/640x480/', 'hiro'),
                new ImageModel(9, 'https://source.unsplash.com/640x480/', 'hiro'),
            ],
            sharedImages: [],
            imageCount: 0,
            row1: new RowModel(),
            row2: new RowModel(),
            row3: new RowModel(),
            fetching: false
        }),
        computed: {
            firstRow() {
                return true
            }
        },
        mounted() {
            this.distribute(this.getImages())
            this.addOnScroll()
        },
        methods: {
            addOnScroll() {
                window.onscroll = () => {
                  if(!this.fetching) {
                    let value = document.documentElement.scrollTop + window.innerHeight
                    let bottomOfWindow = document.documentElement.offsetHeight - value <= 1000
                    //console.log(value +  ' / ' + document.documentElement.offsetHeight)
                    if (bottomOfWindow) {
                      //console.log('hitted')
                      this.fetching = true
                      let self = this
                     setTimeout(function () {
                        let images = self.getImages(self.imageCount - 1)
                        self.distribute(images)
                        self.fetching = false
                      }, 5000)





                    }
                  }
                }


            },
            getImages(last = -1) {
                var images = []
                var i = last + 1
                while (i < last + Math.floor(Math.random() * 100 * 3) + 7) {
                    images.push(new ImageModel(i, 'https://source.unsplash.com/640x480/?africa', 'hiro'))
                    i++
                }

                return images
            },
            onScroll: function () {
                console.log(window.scrollY)
            },
            setRandomHeight(image) {
                const a = Math.floor(Math.random() * 10)
                if ([0, 1, 3, 4].includes(a)) {
                    image.setLong()
                } else if (a === 2 || a === 5) {
                    image.setShort()
                }
                return image
            },
            distribute(images) {


                const itemPerRow = Math.floor(images.length / 3)
                const longPerRow = Math.floor(Math.random() * itemPerRow / 2)
                const normalPerRow = Math.floor(Math.random() * (itemPerRow - longPerRow))
                const shortPerRow = itemPerRow - (longPerRow + normalPerRow)

                console.log('Short: ' + shortPerRow)
                console.log('Normal: ' + normalPerRow)
                console.log('Long: ' + longPerRow)
                console.log('Items: ' + itemPerRow)
                for (let row of [this.row1, this.row2, this.row3]) {
                    let longRemaining = longPerRow
                    let shortRemaining = shortPerRow
                    let normalRemaining = normalPerRow

                    for (let i = 0; i < itemPerRow; i++) {
                        if (images.length < 1) {
                            break
                        }
                        let tempImage = images.pop()
                        while (i > -1) {
                            let random = Math.floor(Math.random() * 3)
                            if (random === 0 && shortRemaining > 0) {
                                this.imageCount++
                                tempImage.setShort()
                                row.addImage(tempImage)
                                shortRemaining--
                                break
                            } else if (random === 1 && normalRemaining > 0) {
                                this.imageCount++
                                tempImage.setNormal()
                                row.addImage(tempImage)
                                normalRemaining--
                                break
                            } else if (random === 2 && longRemaining > 0) {
                                this.imageCount++
                                tempImage.setLong()
                                row.addImage(tempImage)
                                longRemaining--
                                break
                            }
                        }


                    }

                }

                console.log('1 Long:' + this.row1.longImages + ' Normal: ' + this.row1.normalImages + ' Short: ' + this.row1.shortImages + ' Total: ' + this.row1.count)
                console.log('2 Long:' + this.row2.longImages + ' Normal: ' + this.row2.normalImages + ' Short: ' + this.row2.shortImages + ' Total: ' + this.row2.count)
                console.log('3 Long:' + this.row3.longImages + ' Normal: ' + this.row3.normalImages + ' Short: ' + this.row3.shortImages + ' Total: ' + this.row3.count)

            }
        },
        components: {
            ImageTile
        }
    }
</script>
