export class RowModel {
    constructor() {
        this.images = []
        this.longImages = 0;
        this.shortImages = 0;
        this.normalImages = 0;
    }

    addImage(image){
        if (image.long) {
            this.longImages++
        }else if (image.short) {
            this.shortImages++
        }else {
            this.normalImages++
        }
        this.images.push(image)
    }

    get count() {
        return this.images.length
    }
}