<script>
    import Logo from "./Logo";
    export default {
        name: "TopNav",
        components: {Logo},
        data: () => ({
            menuActive: false,
            moreMenuActive: false,
            transparent: false,
            watchRoute: ['Home']
        }),

        methods: {
            shouldWatch() {
                console.log(this.$route.name)
                return this.watchRoute.includes(this.$route.name)
            },
            toggleMenu: function () {
                this.menuActive = !this.menuActive
                this.moreMenuActive = false
            },
            toggleMoreMenu: function () {
                this.moreMenuActive = !this.moreMenuActive
                this.menuActive = false
            },
            navSpy() {
                console.log('NAV SPY ACTIVe')
                console.log(window.scrollY)
                if (window.scrollY > window.innerHeight / 3) {
                    this.transparent = false
                } else {
                    this.transparent = true
                }
            },
            routeWatcher() {
                console.log('Route watcher')
                if (this.shouldWatch()) {
                    this.navSpy()
                    window.addEventListener("scroll", this.navSpy)
                }
                this.$watch(function () {
                    return this.$route
                }, function (route) {
                    console.log(route.name)
                    if (this.shouldWatch()) {
                        this.navSpy()
                        window.addEventListener("scroll", this.navSpy)
                    } else {
                        window.removeEventListener("scroll", this.navSpy)
                        this.transparent = false
                    }
                })
            }
        },
        mounted() {
            this.routeWatcher()
        }

    }
</script>

<style scoped>
    .router-link-exact-active {
        @apply text-poro-primary;
    }
</style>